/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html {
	font-family: sans-serif;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}

body {
	margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
	display: block;
}

audio,
canvas,
progress,
video {
	display: inline-block;
	vertical-align: baseline;
}

audio:not([controls]) {
	display: none;
	height: 0;
}

[hidden],
template {
	display: none;
}

a {
	background-color: transparent;
}

a:active,
a:hover {
	outline: 0;
}

abbr[title] {
	border-bottom: 1px dotted;
}

b,
strong {
	font-weight: bold;
}

dfn {
	font-style: italic;
}

h1 {
	font-size: 2em;
	margin: 0.67em 0;
}

mark {
	background: #ff0;
	color: #000;
}

small {
	font-size: 80%;
}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sup {
	top: -0.5em;
}

sub {
	bottom: -0.25em;
}

img {
	border: 0;
}

svg:not(:root) {
	overflow: hidden;
}

figure {
	margin: 1em 40px;
}

hr {
	box-sizing: content-box;
	height: 0;
}

pre {
	overflow: auto;
}

code,
kbd,
pre,
samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
	color: inherit;
	font: inherit;
	margin: 0;
}

button {
	overflow: visible;
}

button,
select {
	text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: button;
	cursor: pointer;
}

button[disabled],
html input[disabled] {
	cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
	border: 0;
	padding: 0;
}

input {
	line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	height: auto;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}

legend {
	border: 0;
	padding: 0;
}

textarea {
	overflow: auto;
}

optgroup {
	font-weight: bold;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

td,
th {
	padding: 0;
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
	color: #666;
	font-family: "Roboto Condensed", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1.5;
}

@media screen and (min-width: 700px) {
	body,
	button,
	input,
	select,
	optgroup,
	textarea {
		font-size: 16px;
		font-size: 1rem;
	}
}

h1, h2, h3, h4, h5, h6 {
	margin-top: .75em;
	margin-bottom: .25em;
	clear: both;
	font-family: "Playfair Display", Georgia, Cambria, "Times New Roman", Times, serif;
}

h1 {
	font-size: 2.5em;
}

h2 {
	font-size: 2.3em;
}

h3 {
	font-size: 2em;
}

h4 {
	font-size: 1.8em;
}

h5 {
	font-size: 1.6em;
}

h6 {
	font-size: 1.4em;
}

p {
	margin-bottom: 1.5em;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	position: relative;
	margin: 0 0 1em;
	background: #fff9c0;
	padding: 1em 2em .5em 5em;
	font-style: italic;
}

blockquote:before {
	position: absolute;
	top: -.2em;
	left: -.13em;
	content: "“";
	font-family: "Roboto Condensed", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	font-size: 12em;
	line-height: 100%;
	color: white;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 18.75px;
	font-size: 1.17188rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 18.75px;
	font-size: 1.17188rem;
}

abbr, acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark, ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	/* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
	box-sizing: inherit;
}

body {
	background: #fff7d1;
	/* Fallback for when there is no custom background color defined. */
}

blockquote, q {
	quotes: "" "";
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul, ol {
	margin: 0 0 1.5em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: .5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

img {
	height: auto;
	/* Make sure images are scaled correctly. */
	max-width: 100%;
	/* Adhere to container width. */
}

figure {
	margin: 0;
	/* Extra wide images within figure tags don't overflow the content area. */
}

table {
	border-collapse: collapse;
	margin: 0 0 1.5em;
	width: 100%;
}

thread th {
	border-bottom: 2px solid #bbb;
	padding-bottom: .5em;
}

td,
th {
	vertical-align: top;
}

th {
	padding: .4em;
	text-align: left;
}

tr {
	border-bottom: 1px solid #eee;
}

td {
	padding: .4em;
}

th:first-child,
td:first-child {
	padding-left: 0;
}

th:last-child,
td:last-child {
	padding-right: 0;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: #ccc;
	border-radius: 3px;
	background: #d9d9d9;
	font-size: 18px;
	font-size: 1.125rem;
	line-height: 1;
	padding: .45em 0.8em .4em;
}

button:hover, button:active, button:focus,
input[type="button"]:hover,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:hover,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:hover,
input[type="submit"]:active,
input[type="submit"]:focus {
	color: #fff;
	background-color: #eb4646;
	border-color: #eb4646;
	outline: none;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
	margin-bottom: 2px;
	margin-right: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	color: #666;
	text-decoration: none;
}

a:visited {
	color: #666;
}

a:hover, a:focus, a:active {
	color: #eb4646;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

.page-content a,
.entry-content a,
.entry-summary a,
.comment-content a {
	text-decoration: none;
	border-bottom: 2.5px solid #eb4646;
}

.page-content a:focus, .page-content a:hover,
.entry-content a:focus,
.entry-content a:hover,
.entry-summary a:focus,
.entry-summary a:hover,
.comment-content a:focus,
.comment-content a:hover {
	color: #eb4646;
}

.edit-link a {
	display: inline-block;
	padding: .5em 1.4em;
	font-family: "Roboto Condensed", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	font-size: 85%;
	line-height: 1.3em;
	text-decoration: none;
	border: 1px solid #c3c3c3;
	border-radius: 3px;
}

.edit-link a:hover, .edit-link a:focus {
	color: #fff;
	background: #eb4646;
	border-color: #eb4646;
}

/*--------------------------------------------------------------
## Content Navigation
--------------------------------------------------------------*/
.comment-navigation,
.posts-navigation,
.post-navigation,
.paging-navigation {
	padding: 1em 0;
	font-family: "Roboto Condensed", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	text-align: left;
	overflow: hidden;
}

.comment-navigation a,
.posts-navigation a,
.post-navigation a,
.paging-navigation a {
	display: block;
	width: 100%;
	text-decoration: none;
	padding: 1em;
	border: 1px solid #cccccc;
}

.comment-navigation a:hover, .comment-navigation a:focus,
.posts-navigation a:hover,
.posts-navigation a:focus,
.post-navigation a:hover,
.post-navigation a:focus,
.paging-navigation a:hover,
.paging-navigation a:focus {
	color: #fff;
	border-color: #eb4646;
	background: #eb4646;
}

.nav-links {
	margin: 0 auto;
	padding: 0;
}

.nav-previous,
.nav-next {
	float: left;
	display: flex;
	background: #fff;
	justify-content: space-between;
	width: 48%;
}

.nav-next {
	float: right;
	text-align: right;
}

.post-navigation .meta-nav {
	display: block;
}

@media screen and (min-width: 30em) {
	.post-navigation {
		padding-bottom: 1em;
	}
}

.post-navigation .post-title {
	font-size: 1.2em;
	font-weight: 700;
	border-bottom: 2px solid #eb4646;
}

.post-navigation a:hover .post-title,
.post-navigation a:focus .post-title {
	border-bottom-color: white;
}

.pagination {
	font-family: "Roboto Condensed", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
}

.pagination .nav-links {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}

.pagination a,
.pagination span {
	padding: .5em 1em;
	background: white;
}

.pagination a {
	text-decoration: none;
}

.pagination a:focus, .pagination a:hover {
	color: white;
	background: #eb4646;
}

.pagination .current {
	font-weight: 600;
	color: #eb4646;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
	word-wrap: normal !important;
	/* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #666;
	display: block;
	font-size: 17.5px;
	font-size: 1.09375rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
	table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-content:after,
.site-footer:after {
	clear: both;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Global layouts
--------------------------------------------------------------*/
.site-content {
	padding: 3em 1em 1em;
	max-width: 1100px;
	margin: 0 auto;
}

@media screen and (min-width: 600px) {
	.site-content {
		padding: 3em 2em 2em;
	}
}

.single.has-sidebar .entry-content,
.page .entry-content {
	background: #fff;
	box-shadow: 12px 12px 20px 0 rgba(46, 61, 73, 0.15);
	padding: 2em 1.5em;
}

@media screen and (min-width: 900px) {
	.single.has-sidebar .hentry,
	.page .hentry {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.single.has-sidebar .hentry .entry-header,
	.page .hentry .entry-header {
		width: 100%;
		flex: 1 0 100%;
	}
	.single.has-sidebar .hentry .post-content,
	.page .hentry .post-content {
		width: 70%;
	}
	.single.has-sidebar .hentry .widget-area,
	.page .hentry .widget-area {
		width: 25%;
	}
	.single.has-sidebar .hentry .entry-content,
	.page .hentry .entry-content {
		padding: 2em 1.5em;
	}
	.page.has-page-sidebar .hentry {
		flex-direction: row-reverse;
	}
}

@media screen and (min-width: 900px) {
	.single.no-sidebar .post-content__wrap {
		display: flex;
		justify-content: space-between;
	}
	.single.no-sidebar .post-content__wrap .entry-meta {
		width: 20%;
	}
	.single.no-sidebar .post-content__wrap .post-content__body {
		width: 70%;
	}
}

/*--------------------------------------------------------------
## Archive
--------------------------------------------------------------*/
.archive-view .post,
.archive-view .page {
	position: relative;
	background: #fff;
	box-shadow: 12px 12px 20px 0 rgba(46, 61, 73, 0.15);
}

.archive-view .post__content {
	padding: 1.5em;
}

@media screen and (min-width: 900px) {
	.archive-view.has-sidebar .site-content {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	.archive-view.has-sidebar .post,
	.archive-view.has-sidebar .page {
		margin-right: 1em;
	}
	.archive-view.has-sidebar .page-header {
		width: 100%;
		flex: 1 0 100%;
	}
	.archive-view.has-sidebar .content-area {
		width: 70%;
	}
	.archive-view.has-sidebar #secondary {
		width: 25%;
	}
}

.page-title {
	margin: 0 0 .8em;
}

.archive-description {
	margin: -1em 0 2em;
}

/* For content-none.php */
.search .page-content,
.error-404 .page-content {
	margin-bottom: 2em;
	padding: 1em 2em;
	font-family: "Roboto Condensed", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	color: white;
	background: #eb4646;
}

.search input[type="search"],
.error-404 input[type="search"] {
	padding: .5em;
}

.search input[type="submit"],
.error-404 input[type="submit"] {
	border-color: white;
	color: white;
}

.search input[type="submit"]:focus, .search input[type="submit"]:hover,
.error-404 input[type="submit"]:focus,
.error-404 input[type="submit"]:hover {
	color: #666;
}

.sticky::before {
	content: "Featured";
	display: inline-block;
	position: absolute;
	top: 0px;
	float: right;
	color: #fff;
	background: #eb4646;
	padding: 1px 6px;
	text-transform: uppercase;
	font-style: italic;
}

.index-image img {
	display: block;
	margin: 0 auto;
}

.index-image img:focus, .index-image img:hover {
	opacity: .8;
}

.read-more {
	position: absolute;
	right: 0;
	bottom: 0;
	font-family: "Roboto Condensed", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
}

.read-more a {
	display: block;
	padding: .5em 1em;
	font-size: 90%;
	text-decoration: none;
	background: #d9d9d9;
}

.read-more a:focus, .read-more a:hover {
	color: white;
	background: #eb4646;
}

/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
.header-image {
	max-height: 60vh;
	overflow: hidden;
}

.header-image img {
	display: block;
	width: 100vw;
}

.site-header {
	position: relative;
	padding: 1em;
	height: 110px;
	font-family: "Roboto Condensed", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	color: #fff;
	background-color: #002254;
	z-index: 999;
	box-shadow: 0 2px 3px rgba(0, 0, 0, 0.4);
}

@media screen and (min-width: 600px) {
	.site-header {
		padding: 1em 2em;
	}
}

.site-branding {
	position: relative;
	display: flex;
	margin-left: -1em;
	min-height: 75px;
	flex: 1 0 auto;
	font-size: 75%;
}

@media screen and (min-width: 600px) {
	.site-branding {
		font-size: 100%;
	}
}

.custom-logo-link {
	margin-right: .5em;
}

.custom-logo-link img {
	display: block;
	height: 75px;
	width: auto;
}

.site-branding__text {
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 75px;
}

.site-title {
	margin: 0;
	padding: 0;
	font-weight: 700;
	line-height: 1em;
}

.site-title a {
	color: white;
}

.site-title a:hover, .site-title a:focus {
	color: #eb4646;
}

.site-description {
	margin: 0;
	font-size: 1.2em;
	font-style: italic;
	font-weight: 100;
}

@media screen and (min-width: 900px) {
	.site-header {
		display: flex;
		justify-content: space-between;
	}
	.site-branding {
		width: 40%;
	}
	.main-navigation {
		width: 60%;
	}
}

/*Header
*/
.main-navigation {
	display: block;
	font-family: "Roboto Condensed", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	font-weight: 400;
	clear: left;
	background: #002254;
	margin-left: -1em;
	margin-right: -1em;
	text-transform: uppercase;
}

.main-navigation .menu-all-pages-container {
	margin-top: 16px;
}

.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-top: .5em;
	padding-left: 0;
}

.main-navigation ul ul {
	display: none;
	top: 1.5em;
	z-index: 99999;
	background: #002254;
}

.main-navigation ul ul ul {
	top: 0;
}

.main-navigation ul ul li {
	padding-left: 1em;
}

.main-navigation ul ul li:hover > ul,
.main-navigation ul ul li:focus > ul {
	left: 100%;
