button.dropdown-toggle {
	position: absolute;
	width: 40px;
	right: 0;
	border: none;
	background: inherit;
	color: #eb4646;
}

button.dropdown-toggle:hover, button.dropdown-toggle:active, button.dropdown-toggle:focus {
	outline: none;
}

.menu-toggle {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 5px;
	cursor: pointer;
	padding: 10px 35px 16px 0px;
	/* Move first and last span to form cross */
}

.menu-toggle:hover span,
.menu-toggle:hover span::before,
.menu-toggle:hover span::after, .menu-toggle:active span,
.menu-toggle:active span::before,
.menu-toggle:active span::after, .menu-toggle:focus span,
.menu-toggle:focus span::before,
.menu-toggle:focus span::after {
	background: #eb4646;
	outline: none;
}

.menu-toggle span,
.menu-toggle span::before,
.menu-toggle span::after {
	cursor: pointer;
	border-radius: 1px;
	height: 4px;
	width: 35px;
	background: #fff;
	position: absolute;
	display: block;
	content: '';
	transition: all 300ms ease-in-out;
}

.menu-toggle span::before {
	top: -10px;
}

.menu-toggle span::after {
	bottom: -10px;
}

.menu-toggle.active span {
	background-color: transparent;
}

.menu-toggle.active span:before, .menu-toggle.active span:after {
	top: 0;
}

.menu-toggle.active span:before {
	transform: rotate(45deg);
}

.menu-toggle.active span:after {
	transform: rotate(-45deg);
}

/* Toggle small menu and sub-menus on */
.toggled-on ul,
.children.toggled-on,
.sub-menu.toggled-on {
	display: block;
}

@media screen and (min-width: 899px) {
	.menu-toggle {
		display: none;
